@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Oswald-Regular.woff2") format("woff2"), url("/fonts/Oswald-Regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Oswald-SemiBold.woff2") format("woff2"), url("/fonts/Oswald-SemiBold.woff") format("woff");
}
*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}
.wrapper--little {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}
.container {
  box-sizing: border-box;
  margin: 0 auto;
  width: 320px;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}
.container--paddings {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container--paddings {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container--paddings {
    padding: 0 110px;
  }
}
.button {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 10px;
  width: 280px;
  height: 40px;
  background-color: #68b738;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  font-family: "Oswald", "Times New Roman", Times, serif;
  font-weight: 400;
}
@media (min-width: 768px) {
  .button {
    width: 244px;
    height: 54px;
    padding: 15px 25px;
    font-size: 20px;
    line-height: 24px;
    color: #ffffff;
    font-family: "Oswald", "Times New Roman", Times, serif;
    font-weight: 400;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.6);
}
.button--grey {
  background-color: #f2f2f2;
  color: #444444;
}
.button--grey:hover {
  background-color: #ebebeb;
}
.button--grey:active {
  color: rgba(68, 68, 68, 0.6);
}
.page {
  height: 100%;
  min-height: 100%;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-family: "Oswald", "Times New Roman", Times, serif;
  font-weight: 400;
}
.page-body {
  min-height: 100%;
