.form-label-radio {
  --radio-outer-diameter: 30px;
  --radio-inner-diameter: 13px;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 46px;
  min-height: var(--radio-outer-diameter);
  font-size: 16px;
  line-height: 18px;
  color: #222222;
  font-family: "Oswald", "Times New Roman", Times, serif;
  font-weight: 400;
}
@media (min-width: 768px) {
  .form-label-radio {
    --radio-outer-diameter: 40px;
    --radio-inner-diameter: 18px;
    padding-left: 59px;
    font-size: 20px;
    line-height: 20px;
    color: #222222;
    font-family: "Oswald", "Times New Roman", Times, serif;
    font-weight: 400;
  }
}
.form-label-radio:active {
  opacity: 0.3;
}
.form-label-radio::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: var(--radio-outer-diameter);
  height: var(--radio-outer-diameter);
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  background-color: white;
}
.form-label-radio::after {
  content: none;
  position: absolute;
  left: calc((var(--radio-outer-diameter) - var(--radio-inner-diameter)) / 2);
  top: 50%;
  transform: translateY(-50%);
  width: var(--radio-inner-diameter);
  height: var(--radio-inner-diameter);
  border-radius: 50%;
  background-color: #68b738;
}
.form-label-radio:hover::before {
  border-color: #bababa;
}
.form-label-checkbox {
  --checkbox-width: 30px;
  --checkbox-height: 30px;
  --checkbox-tick-width: 16px;
  --checkbox-tick-height: 8px;
  cursor: pointer;
  font-size: 16px;
  line-height: 18px;
  color: black;
  font-family: "Oswald", "Times New Roman", Times, serif;
  font-weight: 400;
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 45px;
  min-height: var(--checkbox-height);
}
@media (min-width: 768px) {
  .form-label-checkbox {
    --checkbox-width: 40px;
    --checkbox-height: 40px;
    --checkbox-tick-width: 18px;
    --checkbox-tick-height: 9px;
    font-size: 20px;
    line-height: 20px;
    color: black;
    font-family: "Oswald", "Times New Roman", Times, serif;
    font-weight: 400;
    padding-left: 59px;
  }
}
.form-label-checkbox:active {
  opacity: 0.3;
}
.form-label-checkbox::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: var(--checkbox-width);
  height: var(--checkbox-height);
  border: 1px solid #cdcdcd;
  background-color: white;
}
.form-label-checkbox::after {
  content: none;
  position: absolute;
  left: calc((var(--checkbox-width) - var(--checkbox-tick-width)) / 2);
  top: calc((var(--checkbox-width) - var(--checkbox-tick-width)) / 2);
  width: var(--checkbox-tick-width);
  height: var(--checkbox-tick-height);
  transform: rotate(-45deg);
  border-left: 2px solid #68b738;
  border-bottom: 2px solid #68b738;
}
.form-label-checkbox:hover::before {
  border-color: #bababa;
}
.form__radio:checked + .form__label-radio::after,
.form__checkbox:checked + .form__label-checkbox::after {
  content: "";
}
.form__radio:focus + .form__label-radio,
.form__checkbox:focus + .form__label-checkbox {
  outline-offset: 2px;
}
.form__radio:disabled + .form__label-radio,
.form__checkbox:disabled + .form__label-checkbox {
  opacity: 0.5;
}
